.title {
  text-align: center;
  font-weight: bold;
  margin-top: 50px;
  margin-bottom: 20px;
  color: rgb(88, 0, 74);
  font-size: 30px;
}

.error_message {
  color: red;
  font-weight: bold;
}

@media screen and (max-width: 1200px) {
  .title {
    margin-top: 40px;
  }
  .input,
  textarea {
    margin: 0;
    padding: 0;
  }
}

.form_container {
  width: 70%;
  margin: 0 auto;
  text-align: left;
}

form {
  width: 100%;
  /* margin: 10% auto;
    min-width: 9rem; */
  box-sizing: border-box;
  height: 70%;
}

.input,
textarea {
  box-sizing: border-box;
  width: 100%;
  /* max-width: 100%; */
  border: none;
  margin: 10px 0;
  padding: 15px 30px 15px 10px;
  border-radius: 0.3rem;
  background: darken(#f9f9f9, 10%);
  color: darken(#f9f9f9, 50%);
}

.submit {
  background: green;
  color: #fff;
  width: auto;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
  display: inline-block;
  margin-left: auto;
  display: flex;
  justify-content: end;
}

.submit:hover {
  opacity: 0.7;
}
