.root {
  width: 80%;
  height: 500px;
  background-color: #fff;
  margin: 0 auto;
  margin-top: 4%;
  overflow: hidden;
  border-radius: 30px;
  object-fit: cover;
}

.pictureArea {
  height: auto;
  width: 100%;
}
.img {
  width: 100%;
  object-fit: contain;
  height: 320px;
}

.messageArea {
  box-sizing: border-box;
  padding: 20px;
}

.title {
  text-align: center;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 24px;
}

@media screen and (max-width: 1200px) {
  .root {
    margin-top: 12%;
  }
  .pictureArea {
    height: 280px;
  }
  .img {
    height: 280px;
  }

  .messageArea {
    box-sizing: border-box;
    padding: 10px;
    font-size: 14px;
  }
}
